body {
  font-family: "Montserrat", sans-serif;
  font-size: 62.5%;
  background-color: #F5F5F7;
  color: #262626;
}

.header {
  padding: 20px 15px;
  background-color: #F5F5F7;
}
.header .logo-part {
  width: 112px;
  display: block;
}
.header .logo-part img {
  width: 100%;
}

.main-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav {
  opacity: 0;
  pointer-events: none;
  position: absolute;
  background-color: #F5F5F7;
  left: 0px;
  width: 100%;
  transform: translateX(-100%);
  transition: all ease-in-out 0.3s;
}
.nav-ul {
  list-style: none;
  padding: 15px 15px;
}
.nav-li {
  margin-bottom: 10px;
}
.nav-link {
  font-family: inherit;
  font-size: 1.6rem;
  color: #262626;
  text-align: center;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  text-decoration: none;
}
.nav-link:hover {
  color: #3A55C1;
}

.nav.active {
  opacity: 1;
  pointer-events: all;
  transform: translateX(0%);
}

.menu-lines {
  cursor: pointer;
}

.other-info {
  display: flex;
}

.phone-button {
  margin-right: 40px;
  margin-top: 1px;
}

.dropdown {
  position: relative;
  display: inline-block;
  width: 100%;
}
.dropdown .nav-link:first-child::after {
  content: "";
  display: block;
  position: absolute;
  left: 50%;
  top: 13px;
  width: 15px;
  height: 7px;
  background-image: url("../img/icons/arrdown.svg");
  background-repeat: no-repeat;
  background-size: contain;
}

.dropdown-content {
  display: none;
  min-width: 160px;
  padding: 15px 10px 0px 10px;
  z-index: 1;
  list-style: none;
}
.dropdown-content .nav-link {
  font-size: 1.4rem;
}

.dropdown:hover .nav-link:first-child:after svg {
  fill: #3A55C1;
}

.dropdown:hover .dropdown-content {
  display: block;
}